import Vue from 'vue'
import Vuex from 'vuex'
import API from './api'
import {Base64} from 'js-base64'

Vue.use(Vuex)

const repo = {owner: "ruanyf", repo: 'es6tutorial'};

export default new Vuex.Store({
    state: {
        summary: []
    },
    mutations: {
        setSummary: function (state, summary) {
            state.summary = summary;
        }
    },
    actions: {
        getSummary: async function ({commit}) {
            API.getContents({...repo, path: 'SUMMARY.md'})
                .then(res => Base64.decode(res.content))
                .then(res => {
                    const lines = res.split('\n').slice(1).filter(value => value.length > 0);
                    const summary = lines.map((line, index) => {
                        const name = /\[\d+.\s(.+)\]/.exec(line)[1];
                        const path = /\((.+)\)/.exec(line)[1];
                        return {
                            index,
                            name,
                            path
                        };
                    });
                    commit('setSummary', summary);
                });
        },
        getContent: function (context, path) {
            return API.getContents({...repo, path}).then(res => {
                return Base64.decode(res.content);
            });
        }
    }
})
