<template>
    <v-app id="inspire">
        <v-navigation-drawer
                v-model="drawer"
                :clipped="$vuetify.breakpoint.lgAndUp"
                fixed
                app>
            <v-list dense>
                <v-list-tile
                        v-for="item in summary"
                        @click="active = item"
                        v-bind:class="{activated: item === active}"
                        :key="item.path">
                    <v-list-tile-content>
                        <v-list-tile-title>{{ item.name }}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar
                :clipped-left="$vuetify.breakpoint.lgAndUp"
                color="blue darken-3"
                dark
                app
                fixed>
            <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
                <span class="hidden-sm-and-down">ES6 Book</span>
            </v-toolbar-title>
            <v-autocomplete
                    v-model="keyword"
                    flat
                    solo-inverted
                    hide-details
                    clearable
                    prepend-inner-icon="search"
                    label="搜索"
                    :items="items"
                    class="hidden-sm-and-down"
            ></v-autocomplete>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-content>
            <transition name="fade">
                <div class="markdown-body" v-if="html" v-html="html"></div>
            </transition>
        </v-content>
    </v-app>
</template>

<script>
    import {mapState} from 'vuex';
    import marked from 'marked';
    import highlight from 'highlight.js';

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function (code) {
            return highlight.highlightAuto(code).value;
        },
        pedantic: false,
        gfm: true,
        tables: true,
        breaks: false,
        sanitize: false,
        smartLists: true,
        smartypants: false,
        xhtml: false
    });

    export default {
        name: 'App',
        beforeCreate: function () {
            this.$store.dispatch('getSummary');
        },
        computed: {
            ...mapState(["summary"]),
            items: function () {
                return this.summary.map(value => value.name);
            }
        },
        watch: {
            active: function (val) {
                this.getContent(val);
            },
            keyword: function (val) {
                if (!val || val === '' || (this.active && this.active.name === val)) {
                    return;
                }
                const result = this.summary.find(value => value.name === val);
                this.active = result;
                this.keyword = "";
                if (result) {
                    this.getContent(result);
                }
            }
        },
        methods: {
            getContent: function (item) {
                this.html = null;
                if (item.content) {
                    this.html = marked(item.content);
                } else {
                    this.$store.dispatch('getContent', item.path)
                        .then(content => {
                            this.html = marked(content);
                            // item.content = content;
                        });
                }
            }
        },
        data: function () {
            return {
                drawer: null,
                active: null,
                html: null,
                keyword: null
            }
        }
    }
</script>

